import * as THREE from "three";
import React, { useEffect, Component } from "react";
import ReactDOM from "react-dom";
import { render } from "@testing-library/react";
import { Model } from "../Models/ModelDisplay";
import {PerspectiveCamera, OrbitControls, Box, Plane } from "@react-three/drei/native";
import { ARCanvas} from "react-xr";
import {Canvas, useThree, useFrame} from 'react-three-fiber'
import { Physics, useBox, usePlane } from "@react-three/cannon";

const ThreeJSModelViewer = (props) => {


  function PhyPlane({ color, ...props }) {
    const [ref] = usePlane(() => ({ ...props }));
  
    return (
      <Plane args={[1000, 1000]} ref={ref}>
        <meshStandardMaterial color={color} />
      </Plane>
    );
  }

  function PhyBox(props) {
    const [ref, api] = useBox(() => ({ args: [1, 1, 1], mass: 1, ...props }));
  
    return (
      <Box
        args={[1, 1, 1]}
        ref={ref}
        onClick={() => api.applyImpulse([0, 0, -10], [0, 0, 0])}
      >
        <meshNormalMaterial />
      </Box>
    );
  }
  return (
    <ARCanvas camera={{ position: [0, 0, 0], near: 0.1, far: 1000 }} resize={{ scroll: false }}>
      <Physics gravity={[0, -10, 0]}>
        <PhyPlane
          color="hotpink"
          position={[0, -2, 0]}
          rotation={[-Math.PI / 2, 0, 0]}
        />
        <PhyPlane color={props.color} position={[0, 0, -10]} />
        <PhyBox position={[2, 0, -5]} />
        <PhyBox position={[0, 0, -5]} />
        <PhyBox position={[-2, 0, -5]} />
      </Physics>
      <ambientLight intensity={0.3} />
      <pointLight intensity={0.8} position={[5, 0, 5]} />
    </ARCanvas>
  );
};
  

 export default ThreeJSModelViewer;
